html, body {
  width: 100%;
  height: 100%;
  background-color: rgb(24, 24, 24);
  display: flex;
  justify-content: center;
  align-items: center;
}

.text-4xl {
  width: 100%;
  text-align: center;
  color: white;
}

.buttons {
  width: 100%;
  display: flex;
  justify-content: center;
}

.buttons > button {
  margin: 20px;
  padding: 20px;
  min-width: 200px;
  width: fit-content;
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 36px;
}

.buttongreen {
  background-color: green;
}

.buttonred {
  background-color: red;
}